function dateTime(timestamp, type){
  const milliseconds = timestamp * 1000
  const dataObj = new Date(milliseconds)
  let result = 
    type == 'hours' ? dataObj.toLocaleString("ru-RU", {hour: 'numeric'}): 
    type == 'minutes' ? dataObj.toLocaleString("ru-RU", {minute: 'numeric'}): 
    type == 'day' ? dataObj.toLocaleString("ru-RU", {day: 'numeric'}): 
    type == 'month' ? dataObj.toLocaleString("ru-RU", {month: 'long'}): 
    type == 'weekday' ? dataObj.toLocaleString("ru-RU", {weekday: 'long'}): '' 
  return result
}
export default dateTime