import { createStore } from "vuex";
import axios from 'axios';

console.log('store');
export const store = createStore({
  state: {
    apiKey: 'cb8f76325991c658ce6ec4f326120923',
    getFullWeather: null
  },
  getters: {
    getApiKey(state){return state.apiKey},
    getFullWeekly(state){return state.getFullWeather.daily},
    getFullWeather(state){return state.getFullWeather},
  },
  mutations: {
    getFullWeather(state, load){
      state.getFullWeather = load
    }
  },
  actions: {
    async getWeather({commit, getters},query){
      try{
        let geo = await axios.get(`http://api.openweathermap.org/geo/1.0/direct?q=${query}&appid=${getters.getApiKey}`);
        console.log(geo);
        let {lat,lon,name} =  await geo.data[0]
        let result = await axios.get(`https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lon}&exclude=alerts,minutely,hourly&appid=${getters.getApiKey}&units=metric&lang=ru`)
        let fullInfo = result.data
        let weatherObj = {...fullInfo, name: name}
        commit('getFullWeather', weatherObj)
        console.log(weatherObj);
      }catch(err){
        console.log(err);
      }
    }
  },
  modules: {},
});