<script>
import { tSTypeAliasDeclaration } from "@babel/types";
import { mapActions, mapGetters } from "vuex";
import dateTime from './timstemp';

export default {
  components: {},
  data(){
    return{
      query: '',
    }
  },
  computed: {
    ...mapGetters(['getFullWeather', 'getFullWeekly']),
    getHours(){
      let hours = dateTime(this.getFullWeather.current.dt, 'hours')
      let minutes = dateTime(this.getFullWeather.current.dt, 'minutes')
      return hours + ':' + minutes
    },
    getDays(){

      let day = dateTime(this.getFullWeather.current.dt, 'day')
      let month = dateTime(this.getFullWeather.current.dt, 'month')
      return day + month
    },
       getDescription(){
      return this.getFullWeather.current.weather[0].description
    },
    getdaily(){
      this.getFullWeekly.shift()
      return this.getFullWeekly
    },
    
  },
  methods: {
    ...mapActions(['getWeather']),

  },
  created(){
    this.getWeather('Tashkent')
  }
}
